* {
    margin: 0;
    padding: 0;
}

html, body {
    height: 100%;
}

body {
    font-family: "Open Sans";

    > .ember-view {
        height: 100%;
    }
}

.index-page {
    width: 100%;
    height: 100%;

    .section-title {
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        h1 {
            font-weight: 300;
            line-height: 28px;
            font-size: 28px;
            transition-duration: 1s;
            transition-property: opacity;
            width: 720px;

            > span {
                width: 100%;
            }
        }

        &.hiding {
            h1 {
                opacity: 0;
            }
        }

        &.hidden {
            display: none;
        }
    }

    .history-1 {
        height: 100%;
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;

        &.hidden {
            display: none;
        }

        &.hiding {
            .photos {
                animation: history-1-photos-hide 2s forwards !important;
            }

            .text {
                animation: history-1-text-hide 2s forwards !important;
            }
        }

        .photos {
            width: 400px;
            height: 400px;
            position: relative;

            &.hidden {
                opacity: 0;
            }

            .photo {
                position: absolute;
                top: 50%;
                left: 50%;
                margin-top: -150px;
                margin-left: -150px;
                height: 300px;
                width: 300px;
                background-size: cover;
                background-position: center;
                opacity: 0;
                transition-duration: 1s;
                transition-property: opacity;
                border: 5px solid #FFC107;
                border-radius: 4px;
                box-shadow: 0px 0 1px #535353;
            }

            .photo-0 {
                background-image: url("/assets/images/image_8.jpg");

                &.showing {
                    animation: history-1-image-1-show 1.5s forwards;
                }
            }

            .photo-1 {
                height: 320px;
                margin-top: -160px;
                background-image: url("/assets/images/image_2.jpg");

                &.showing {
                    animation: history-1-image-2-show 1.5s forwards;
                }
            }

            .photo-2 {
                height: 360px;
                margin-top: -180px;
                background-image: url("/assets/images/image_3.jpg");

                &.showing {
                    animation: history-1-image-3-show 1.5s forwards;
                }
            }
        }

        .text {
            width: 400px;
            max-width: 100%;
            font-size: 16px;
            text-indent: 24px;
            text-align: justify;
            opacity: 1;
            transition-duration: 1s;
            transition-property: opacity, transform;
            transform: translateX(-200px);

            &.hidden {
                opacity: 0;
            }

            &.right {
                animation: shift-right 2s forwards;
            }
        }
    }

