    .section-images {
        height: 100%;
        width: 100%;
        padding: 10px 0;

        &.hidden {
            display: none;
        }

        &.visible {
            display: block;
        }

        .items {
            margin: 0 auto !important;

            .items-sizer {
                width: 20%;
            }

            .item {
                width: 20%;
                // width: 260px;
                height: 220px;
                float: left;
                border: 2px solid #fff;
                border-radius: 5px;

                .image {
                    width: 100%;
                    height: 100%;
                    background-size: cover;
                    background-position: center;
                }

                &.item-height-2 {
                    height: 320px;
                }
            }

            &:after {
                content: '';
                display: block;
