@keyframes shift-right {
    to {
        transform: translateX(0);
    }
}

@keyframes history-1-image-1-show {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
        transform: rotateZ(-5deg);
    }
}
@keyframes history-1-image-2-show {
    from {
        opacity: 0;
        transform: translateY(-25px);
    }
    to {
        opacity: 1;
        transform: translateY(0) rotateZ(-20deg);
    }
}
@keyframes history-1-image-3-show {
    from {
        opacity: 0;
        transform: translateX(-25px);
    }
    to {
        opacity: 1;
        transform: translateX(0) rotateZ(15deg);
    }
}
@keyframes history-1-photos-hide {
    to {
        opacity: 0;
        transform: translateY(-100%);
    }
}
@keyframes history-1-text-hide {
    from {
        transform: translateX(0);
    }
    to {
        opacity: 0;
        transform: translateX(100%);
    }
}

